.election__section-title {
  @apply flex gap-1 items-center;
}
.election__section-title svg {
  @apply w-6 h-6 text-tertiary fill-current;
}
.election__section-desc {
  @apply text-gray-2 my-6;
}
.election__accordion {
  @apply border-4 border-background rounded px-3 py-2;
}
.election__accordion-trigger {
  @apply flex justify-between items-center w-full font-bold text-secondary hover:underline focus:outline-none focus:underline;
}
.election__accordion-trigger > :last-child {
  @apply flex-none;
}
.election__accordion-trigger svg {
  @apply w-8 h-8 text-secondary fill-current first:block last:hidden;
}
.election__accordion-trigger[role=button][aria-expanded=true] svg {
  @apply first:hidden last:block;
}
.election__accordion-panel {
  @apply border-t border-gray-3 mt-4 pt-4 space-y-4;
}
.election__accordion-panel ul {
  @apply list-decimal px-4;
}
.election__aside-heading {
  @apply uppercase font-semibold text-gray-2 text-sm;
}
.election__aside-link {
  @apply flex items-center gap-2 text-sm;
}
.election__aside-link svg {
  @apply w-4 h-4 flex-none text-gray fill-current;
}
.election__aside-link a {
  @apply text-md text-secondary;
}

.election-log__entry {
  @apply flex flex-col gap-2 rounded border-4 border-background p-4 mb-8 last:mb-0 text-gray-2;
}
.election-log__hash, .election-log__time {
  @apply text-sm empty:hidden;
}
.election-log__pre {
  @apply bg-background p-2 rounded;
}

.election-question {
  @apply block rounded p-4 border-4 border-background flex items-center text-lg text-gray-2 hover:border-tertiary focus:border-tertiary cursor-pointer;
  /* overwrite default accordion styles */
}
.election-question[aria-disabled=true] {
  @apply text-gray cursor-not-allowed hover:border-background focus:border-background;
}
.election-question__counter {
  @apply font-semibold text-black text-lg bg-tertiary inline-block;
}
.election-question [id^=step][aria-hidden=true] {
  @apply hidden;
}
.election-question__container {
  @apply space-y-4 mb-8;
}
.election-question__intro {
  @apply py-8 border-t-2 border-background space-y-4;
}
.election-question__description {
  @apply text-gray-2 text-lg;
}
.election-question__maxselections {
  @apply flex items-center justify-between gap-2 text-gray-2;
}
.election-question__answer {
  @apply flex flex-col items-start gap-4 py-8 text-gray-2 border-t-2 border-background;
}
.election-question__answer-option {
  @apply flex items-center justify-between w-full text-lg;
}
.election-question__ballot {
  @apply flex flex-col items-start gap-2 py-8 border-t-2 border-background text-lg text-gray-2;
}
.election-question__modal-explanation {
  @apply space-y-4;
}
.election-question__modal-grid {
  @apply grid grid-cols-2 gap-4;
}
.election-question__modal img {
  @apply aspect-video object-cover h-full w-full;
}
.election-question__modal ul {
  @apply list-disc;
}
.election-question__modal li {
  @apply ml-4;
}
.election-question__modal a {
  @apply text-secondary;
}

.election-trustee__container {
  @apply space-y-6 [&_p]:text-gray-2;
}
.election-trustee__list {
  @apply list-disc pl-4 text-gray-2;
}
.election-trustee__table {
  @apply text-left w-full border-2 border-background;
}
.election-trustee__table th,
.election-trustee__table td {
  @apply px-4 py-2;
}
.election-trustee__table th {
  @apply bg-background;
}
.election-trustee__table tr:nth-child(even) td {
  @apply bg-gray-3;
}
.election-trustee__table a {
  @apply text-secondary underline;
}
.election-trustee__table .step_status span {
  @apply hidden;
}
.election-trustee__table .step_status[data-step-status=pending] .pending, .election-trustee__table .step_status[data-step-status=processing] .processing, .election-trustee__table .step_status[data-step-status=completed] .completed {
  @apply block;
}
.election-trustee__submit {
  @apply p-4 border-4 border-background space-y-4 rounded;
}
.election-trustee__buttons {
  @apply flex items-center gap-4;
}
.election-trustee__buttons [hidden] {
  @apply hidden;
}